<template>
	<view class="collect">
		<view class="list">

			<view class="goods-list goods">
				<view class="tis" v-if="goodsList.length == 0">没有数据~</view>
               
					<view class="row" v-for="(row,index) in goodsList" :key="index">
					<!-- 删除按钮 -->
					<view class="menu" @tap.stop="deleteGoods(index,row.goods_id)">
						<view class="icon shanchu"></view>
					</view>
					<!-- 商品 -->
					<view class="carrier" :class="[theIndex==index?'open':oldIndex==index?'close':'']" @touchstart="touchStart(index,$event)"
					 @touchmove="touchMove(index,$event)" @touchend="touchEnd(index,$event)">
					
						<!-- 商品信息 -->
						<view class="goods-info">
							<view class="img">
								<image :src="row.logo"></image>
							</view>
							<view class="info">
								<view class="title"  @tap="toGoods(row)">{{row.title}}</view>
								<view class="spec">{{row.number_sales}}人已购买 &nbsp;&nbsp;{{row.good_percent}}%好评</view>
								<view class="price-number">
									<view class="price">
									     <text>￥{{row.price}}</text>
										 <text>￥{{row.market_price}}</text>
									</view>
									
								</view>
							</view>
						</view>
					</view>
				</view>

			</view>
		</view>
	</view>
</template>

<script>
	import uniSwipeAction from "@/components/uni-swipe-action/uni-swipe-action.vue";

	export default {
		mounted(){
            var _this=this;
			uni.request({
				
				url:this.config.url+"member/collect",
				method:"post",
				data:{
					token:this.token,
					
				},
				success:function(res){
					console.log(res)
					if(res.data.code==1){
						_this.goodsList=res.data.data.data;
					}
					
				}
			})
		},
		components: {
			uniSwipeAction
		},
		data() {
			return {
				options: [{
					text: '删除',
					style: {
						backgroundColor: "rgba(255,84,31,1)"
					}
				}],
				goodsList: [],
				//控制滑动效果
				theIndex: null,
				oldIndex: null,
				isStop: false
			}
		},
		methods: {
			
			bindClick(options,id){
				var _this=this;
				uni.request({
					url:this.config.url+"goods/collect",
					method:"POST",
					data:{
						goods_id:id,
						token:this.token
					},
					success:function(res){
						console.log(res)
						if(res.data.code==1){
							_this.goodsList.splice(options,1)
						}
					}
				})
				console.log(options)
				// this.goodsList=this.goodsList.splice(options,2)
				// console.log(this.goodsList)
			},
			//控制左滑删除效果-begin
			touchStart(index, event) {
				//多点触控不触发
				if (event.touches.length > 1) {
					this.isStop = true;
					return;
				}
				this.oldIndex = this.theIndex;
				this.theIndex = null;
				//初始坐标
				this.initXY = [event.touches[0].pageX, event.touches[0].pageY];
			},
			touchMove(index, event) {
				//多点触控不触发
				if (event.touches.length > 1) {
					this.isStop = true;
					return;
				}
				let moveX = event.touches[0].pageX - this.initXY[0];
				let moveY = event.touches[0].pageY - this.initXY[1];
			
				if (this.isStop || Math.abs(moveX) < 5) {
					return;
				}
				if (Math.abs(moveY) > Math.abs(moveX)) {
					// 竖向滑动-不触发左滑效果
					this.isStop = true;
					return;
				}
			
				if (moveX < 0) {
					this.theIndex = index;
					this.isStop = true;
				} else if (moveX > 0) {
					if (this.theIndex != null && this.oldIndex == this.theIndex) {
						this.oldIndex = index;
						this.theIndex = null;
						this.isStop = true;
						setTimeout(() => {
							this.oldIndex = null;
						}, 150)
					}
				}
			},
			touchEnd(index, $event) {
				//结束禁止触发效果
				this.isStop = false;
			},

			//删除收藏
			
			deleteGoods(index,goodsId) {
				this.goodsList.splice(index,1)
				// let len = this.goodsList.length;
				// for (let i = 0; i < len; i++) {
				// 	if (id == this.goodsList[i].id) {
				// 		this.goodsList.splice(i, 1);
				// 		break;
				// 	}
				// }
				// this.goodsList.splice(this.goodsList.indexOf(id), 1);
				// this.sum();
				// this.oldIndex = null;
				// this.theIndex = null;
				// 删除购物车后台；
				this.delCart(goodsId);
			},
			// 删除购物车
			delCart(id){
				console.log("id",id)
					uni.request({
						url:this.config.url+"goods/collect",
						method:"POST",
						data:{
							token:this.token,
							goods_id:id,
						},

						success:function(res){
							console.log("res",res);
						},
					})
				
			},
		}
	}
</script>

<style lang="scss">
	
	
	@font-face {
		font-family: "HMfont-home";
		src: url('data:application/x-font-woff2;charset=utf-8;base64,') format('woff2');
	}
	
	.icon {
		font-family: "HMfont-home" !important;
		font-size: 60upx;
		font-style: normal;
		color: #000000;
	
		&.jia {
			&:before {
				content: "\e641";
			}
		}
	
		&.jian {
			&:before {
				content: "\e643";
			}
		}
	
		&.shanchu {
			&:before {
				content: "\e6a4";
			}
		}
	
	}
	
	page {
		position: relative;
	}
	.goods-list {
		width: 100%;
		padding: 20upx 0 120upx 0;
		

		.tis {
			display: flex;
			align-items: center;
			justify-content: center;
			font-size: 32upx;
		}
     		.row {
     	width: calc(96%);
     	height: calc(22vw + 40upx);
     	
     
     	
     	display: flex;
     	align-items: center;
     	position: relative;
     	overflow: hidden;
     	z-index: 4;
     	border: 0;
     
     	.menu {
     		.icon {
     			color: #fff;
     			// font-size: 25upx;
     		}
     
     		position: absolute;
     		width: 30%;
     		height: 100%;
     		right: 0;
     		display: flex;
     		justify-content: center;
     		align-items: center;
     		background-color: red;
     		color: #fff;
     		z-index: 2;
     	}
     
     	.carrier {
     		@keyframes showMenu {
     			0% {
     				transform: translateX(0);
     			}
     
     			100% {
     				transform: translateX(-30%);
     			}
     		}
     
     		@keyframes closeMenu {
     			0% {
     				transform: translateX(-30%);
     			}
     
     			100% {
     				transform: translateX(0);
     			}
     		}
     
     		&.open {
     			animation: showMenu 0.25s linear both;
     		}
     
     		&.close {
     			animation: closeMenu 0.15s linear both;
     		}
     
     		background-color: #fff;
     
     		.checkbox-box {
     			padding-left: 20upx;
     			flex-shrink: 0;
     			height: 22vw;
     			margin-right: 20upx;
     		}
     
     		position: absolute;
     		width: 100%;
     		padding: 0 0;
     		height: 100%;
     		z-index: 3;
     		display: flex;
     		align-items: center;
     
     		.goods-info {
     			width: 100%;
     			display: flex;
     			padding-right: 20upx;
                 border-bottom: 1px solid #cecece;
     			padding:60upx 0;
     			.img {
     				width: 22vw;
     				height: 22vw;
     				border-radius: 10upx;
     				overflow: hidden;
     				flex-shrink: 0;
     				margin-right: 10upx;
     
     				image {
     					width: 22vw;
     					height: 22vw;
     				}
     			}
     
     			.info {
     				width: 100%;
     				height: 23vw;
     				overflow: hidden;
     				display: flex;
     				flex-wrap: wrap;
     				position: relative;
                     
     				.title {
     					width: 100%;
     					font-size: 28upx;
     					display: -webkit-box;
     					-webkit-box-orient: vertical;
     					-webkit-line-clamp: 2;
     					// text-align: justify;
     					overflow: hidden;
     				}
     
     				.spec {
     					font-size: 20upx;
     					background-color: #f3f3f3;
     					color: #a7a7a7;
     					height: 30upx;
     					display: flex;
     					align-items: center;
     					padding: 0 10upx;
     					border-radius: 15upx;
     					margin-bottom: 20vw;
     				}
     
     				.price-number {
     					position: absolute;
     					width: 100%;
     					bottom: 0upx;
     					display: flex;
     					justify-content: space-between;
     					align-items: center;
     					font-size: 28upx;
     					height: 60upx;
     					
                          .uni-numbox[data-v-5fc0e502]{
     						 width:140upx;
     						 height:50upx;
     						 display: flex;
     						 align-items: center;
     						
     					 }
     					  .uni-numbox--disabled{
     					 		line-height:100%;
     					 }
     					 .uni-numbox__minus, .uni-numbox__plus{
     					 									 line-height: 100%;
     					 }
     					 .uni-numbox__value[data-v-5fc0e502]{
     					 									 display:flex;
     					 									 justify-content: center;
     					 									 align-items: center;
     					 }
     					.price {}
     
     					.number {
     						display: flex;
     						justify-content: center;
     						margin-right:3upx;
                                
     
     					}
     				}
     			}
     		}
     	}
     }

	}

	
</style>
